  position: relative;

  display: flex;
  flex-direction: column;

  justify-content: center;
  align-items: center;
  align-content: center;
}

li {
  list-style: none;
  position: relative;
  border-radius: 5px;
  margin: 5px;

  width: 95%;
}

.liButton {
  margin-right: 5px;
  margin-bottom: 5px;
  font-size: 12px;
  background-color: #167afbcc;
  opacity: .7;
  transition: .2s ease-in;
}

.liButton:hover {
  opacity: 1;
}

.hide {
  opacity: 0;
  transition: .3s;
  position: absolute;
  right: 0px;
  bottom: 0px;
}

.item:hover > .hide {
  opacity: 1;
}

</style>
